<template>
  <div>
    <!-- 操作框 -->
    <div class="handel-search-wraper">
      <div class="handel-wraper">
        <el-popover
          placement="top-start"
          title="物流公司编号:"
          width="150"
          trigger="hover">
          <p>1-顺丰速派</p>
          <p>2-微特派</p>
          <p>3-顺丰冷运</p>
          <p>4-黑狗冷链</p>
          <p>5-京东快递</p>
          <p>6-申通快递</p>
          <p>7-圆通快递</p>
          <p>8-中通快递</p>
          <p>9-韵达快递</p>
          <p>10-百世快递</p>
          <p>11-天天快递</p>
          <el-button slot="reference" type="primary" @click="doBatchDelivery" size="mini" icon="el-icon-position">批量发货</el-button>
        </el-popover>
      </div>
      <hr class="s-slip">
      <div class="search-form-wraper">
        <el-form :model="searchContent" label-position="top">
          <region-city-store @StoreGroupChange="StoreGroupChange" :showGroup=false></region-city-store>
          <el-form-item label="订单状态">
            <el-select class="search-input" v-model="searchContent.orderStatus" placeholder="请选择订单状态"
              size="small" @change="doSearchHandel(true)">
              <el-option :label="item.label" :value="item.value" :key="item.value" v-for="item in orderStatusOptions">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="订单号">
            <el-input class="search-input" v-model.trim="searchContent.orderId" placeholder="订单号" size="small"
              @keyup.enter.native="doSearchHandel(true)" clearable></el-input>
          </el-form-item>
          <el-form-item label="SKU编码/商品名称">
            <el-input class="search-input" v-model.trim="searchContent.skuName" placeholder="SKU编码/商品名称" size="small"
              @keyup.enter.native="doSearchHandel(true)" clearable></el-input>
          </el-form-item>
          <!-- <el-form-item label="包裹编号">
            <el-input class="search-input" v-model.trim="searchContent.deliveryOrder" placeholder="包裹编号" size="small"
              @keyup.enter.native="doSearchHandel(true)" clearable></el-input>
          </el-form-item> -->
          <!-- <el-form-item label="所属门店">
            <el-select class="search-input" v-model="searchContent.storeNo" filterable placeholder="请选择所属门店" clearable
              size="small" @change="doSearchHandel(true)">
              <el-option v-for="item in storeOptions" :key="item.value" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
          </el-form-item> -->
          <el-form-item label="下单时间">
            <date-picker-range 
              :defaultRange="searchContent.defaultRange"
              :clearable="false"
              :editable="false"
              @datePickerRangeChange="datePickerRangeChange">
            </date-picker-range>
          </el-form-item>
          <el-form-item label="物流公司">
            <el-select class="search-input" v-model="searchContent.companyCode" filterable placeholder="请选择物流公司"
              size="small" @change="doSearchHandel(true)" clearable>
              <el-option v-for="item in companyCodeOptions" :key="item.value" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="物流单号">
            <el-input class="search-input" v-model.trim="searchContent.deliveryCode" placeholder="物流单号" size="small"
              @keyup.enter.native="doSearchHandel(true)" clearable></el-input>
          </el-form-item>
          <el-form-item label="发货日期">
            <el-date-picker class="search-input" v-model="searchContent.deliveryDate" type="date" placeholder="发货时间" format="yyyy-MM-dd" value-format="yyyy-MM-dd" size="small" @change="doSearchHandel(true)">
            </el-date-picker>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="doSearchHandel(true)" size="mini">搜索</el-button>
            <el-button @click="exportData" size="mini" :loading="exportLoading">导出订单</el-button> 
          </el-form-item>
        </el-form>
      </div>
    </div>
    <!-- 搜索结果 -->
    <div class="table-wraper">
      <el-table
        class="fixed-height"
        v-loading="loading"
        :data="tableData"
        empty-text="没有查询到您想要的数据"
        style="width:100%;"
        size="small">
        <el-table-column min-width="170px" prop="orderCreateTime" label="下单时间"></el-table-column>
        <el-table-column min-width="130px" label="门店">
          <template slot-scope="scope" size="small">
            <div>
              {{scope.row.storeNo}}-{{scope.row.storeName}}
            </div>
          </template>
        </el-table-column>
        <el-table-column min-width="160px" label="订单号">
          <template slot-scope="scope" size="small">
            <div class="order-id">
              <router-link :to="scope.row.redirectUrl" target="_blank">{{scope.row.orderId}}</router-link>
            </div>
          </template>
        </el-table-column>
        <!-- <el-table-column min-width="120px" prop="deliveryOrder" label="包裹编号"></el-table-column> -->
        <el-table-column min-width="80px" prop="businessType" :formatter="businessTypeFormatter" label="业务类型"></el-table-column>
        <el-table-column min-width="80px" :formatter="orderStatusFormatter" prop="orderStatus" label="订单状态"></el-table-column>
        <el-table-column min-width="80px" prop="skuNo" label="ERP编码"></el-table-column>
        <el-table-column min-width="120px" prop="erpGoodsName" label="ERP商品名称"></el-table-column>
        <el-table-column min-width="100px" prop="goodsNo" label="SKU编码"></el-table-column>
        <el-table-column min-width="120px" prop="goodsName" label="商品名称"></el-table-column>
        <el-table-column min-width="130px" :show-overflow-tooltip="true" prop="remark" label="订单备注"></el-table-column>
        <el-table-column min-width="100px" label="物流公司">
          <template slot-scope="scope" size="small">
            <div v-if="scope.row.companyCode">
              {{scope.row.companyCode}}-{{scope.row.companyName}}
            </div>
          </template>
        </el-table-column>
        <el-table-column min-width="120px" prop="deliveryCode" label="物流单号"></el-table-column>
        <el-table-column min-width="120px" prop="deliveryDate" label="发货日期">
        </el-table-column>
        <el-table-column fixed="right" label="操作" width="100">
          <template slot-scope="scope">
            <el-button v-if="scope.row.orderStatus === 1" type="text" size="mini" icon="el-icon-position" @click="showBatchInsert(scope.row)">发货
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页条 -->
      <el-pagination
        v-if="total>0"
        class="tc mt20"
        background
        @size-change="changeSize"
        @current-change="changeCurrent"
        :current-page.sync="pageNum"
        :page-sizes="pageSizes"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>

    <excel-add ref="excelAdd" @doConfirmHandel="doConfirmHandel"></excel-add>

    <el-dialog title="发货" :visible.sync="dialogVisible" width='480px'>
      <el-form ref="form" label-width="110px">
        <el-form-item label="物流公司" required>
          <el-select class="search-input" v-model="companyCode" filterable placeholder="请选择物流公司" size="small">
            <el-option v-for="item in companyCodeOptions" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="物流单号" required>
          <el-input class="search-input" v-model.trim="deliveryCode" placeholder="请输入物流单号" size="small" clearable></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false" size="mini">取 消</el-button>
        <el-button type="primary" @click="orderDeliveryHandel" size="mini" :disabled="isDisabled">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import Storage from '@/utils/localStorage'
  import * as utils from '@/utils/utils'
  import config from '@/utils/config'
  import AddByExcel from "@/components/Common/Excel/AddByExcel"
  import exportSheet from '@/utils/exportExcel'
  import Orders from '@/servers/Order'
  import DatePickerRange from '@/components/Common/DatePickerRange'
  import RegionCityStore from '@/components/Common/StoreCommon/RegionCityStore';

  const requestUrl = {
    // search: '/plateform/a/store/condition/search',
    // 获取列表
    getDeliveryOrderList: '/plateform/a/dc/order/list',
    // 批量发货插入
    batchInsert: '/plateform/a/dc/order/batchInsert'
  }
  
  export default {
    data() {
      return {
        pageSizes: config.pageSizes10,
        isDisabled: false,
        dialogVisible: false,
        exportLoading: false, // 导出loading
        loading: false,
        pageNum: 1,
        pageSize: 10,
        total: 0,
        searchContent: {
          DIFFCITY: true,
          orderStatus: 1,
          skuName: '',
          defaultRange: [`${utils.timestampToTimeNoHMS(Date.parse(new Date()) - 7 * 86400000)} 00:00:00`, `${utils.timestampToTimeNoHMS(Date.parse(new Date()))} 23:59:59`],
          beginTime: `${utils.timestampToTimeNoHMS(Date.parse(new Date()) - 7 * 86400000)} 00:00:00`, // 开始时间
          endTime: `${utils.timestampToTimeNoHMS(Date.parse(new Date()))} 23:59:59`, // 结束时间
          deliveryDate: '', // 发货时间
          orderId: '', // 订单号
          deliveryOrder: '', // 包裹编号
          storeNo: '',
          deliveryCode: '', // 发货单号
          companyCode: '', // 物流公司（1 顺丰）
          regionCode: Storage.get('regionCode') || ''
        },
        orderStatusOptions: [
          {
            label: '待发货',
            value: 1
          },
          {
            label: '配送中',
            value: 2
          },
          {
            label: '已完成',
            value: 3
          },
          {
            label: '已退款',
            value: 4
          }
        ],
        businessTypeOptions: [
          {
            label: '常规商品(三方)',
            value: 1
          },
          {
            label: '预售',
            value: 3
          },
          // {
          //   label: '短周期预售',
          //   value: 2
          // },
          {
            label: '及时达',
            value: 4
          },
          {
            label: '拼团',
            value: 5
          },
          {
            label: '虚拟订单',
            value: 6
          }
        ],
        tableData: [], // 表格中查询结果数据
        // 门店
        storeOptions: [],
        companyCodeOptions: [
          {
            label: '1-顺丰速派',
            value: 1
          },
          {
            label: '2-微特派',
            value: 2
          },
          {
            label: '3-顺丰冷运',
            value: 3
          },
          {
            label: '4-黑狗冷链',
            value: 4
          },
          {
            label: '5-京东快递',
            value: 5
          },
          {
            label: '6-申通快递',
            value: 6
          },
          {
            label: '7-圆通快递',
            value: 7
          },
          {
            label: '8-中通快递',
            value: 8
          },
          {
            label: '9-韵达快递',
            value: 9
          },
          {
            label: '10-百世快递',
            value: 10
          },
          {
            label: '11-天天快递',
            value: 11
          }
        ],
        tempConfig: {
          orderId: '订单号（必填）', 
          companyCode: '物流公司编号（必填）', 
          deliveryCode: '物流单号（必填）'
        },

        rowMsg: {},
        companyCode: 1,
        deliveryCode: '',
        exportConfig: {
          orderId: '订单号',
          orderCreateTime: '下单时间',
          pickUpTime: '期望送达/自提时间',
          businessTypeName: '业务类型',
          orderStatusName: '订单状态',
          regionName: '区域',
          cityName: '城市',
          storeNo: '门店编码',
          storeName: '所属门店',
          address: '门店地址',
          telephone: '门店电话',
          rName: '收货人',
          rTel: '收货手机号',
          rAddress: '收货地址',
          remark: '订单备注',
          skuNo: 'ERP编码',
          erpGoodsName: 'ERP商品名称', 
          goodsNo: 'sku编码',
          goodsName: '商品名称',
          goodsSpec: '规格', 
          quantity: '数量',
          companyName: '物流公司',
          deliveryCode: '物流单号',
          deliveryDate: '发货日期',
          regionName: Storage.get('regionName') || ''
        },
        pickerBeginDateBefore: {
          disabledDate: (time) => {
            // this.searchContent.endTime是结束时间date控件的v-model值
            if(!this.searchContent.beginTime) return
            let beginDateVal = new Date(this.searchContent.endTime.replace(/(-)/g, '/'))
            if (beginDateVal) {
              return time.getTime() < beginDateVal.getTime() - 30 * 24 * 60 * 60 * 1000 || time.getTime() > beginDateVal.getTime() || time.getTime() > Date.now()
            }
          }
        },
        pickerBeginDateAfter: {
          disabledDate: (time) => {
            // this.searchContent.beginTime是开始时间date控件的v-model值
            if(!this.searchContent.endTime) return
            let beginDateVal = new Date(this.searchContent.beginTime.replace(/(-)/g, '/'))
            if (beginDateVal) {
              return time.getTime() > beginDateVal.getTime() + 30 * 24 * 60 * 60 * 1000 || time.getTime() < beginDateVal.getTime() || time.getTime() > Date.now()
            }
          }
        },
        regionCode: Storage.get('regionCode') || ''
      }
      
    },
    created() {
      // 获取门店
      // this.getStoreList()
      this.doSearchHandel(true)
    },
    components: {
      "excel-add": AddByExcel,
      "date-picker-range": DatePickerRange,
      "region-city-store": RegionCityStore
    },
    methods: {
      StoreGroupChange(param) {
        this.searchContent.regionCode = param.regionCode
        this.searchContent.cityId = param.cityId
        this.searchContent.storeNo = param.storeNo
        this.doSearchHandel(true)
      },
      /**
       * [getStoreList 获取门店列表]
       * @return {[type]} [description]
       */
       getStoreList() {
        this.$http.post(requestUrl.search).then(res => {
          if (res.code === 0) {
            let optionsArr = res.data && res.data.length ? res.data : []
            this.storeOptions = optionsArr.map(item => {
              return {
                value: item.storeNo,
                label: item.storeNo + '-' + item.storeName
              }
            })
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("提示(获取门店列表):", e.message)
        })
      },
      /**
       * [changeSize description]
       * @param    {[type]}                 value [description]
       * @return   {[type]}                       [description]
       */
      changeSize (value) {
        this.pageSize = value
        this.doSearchHandel()
      },
      /**
       * [changeCurrent 点击分页条]
       * @param    {[type]}                 value [当前页]
       * @return   {[type]}                       [description]
       */
      changeCurrent (value) {
        this.pageNum = value
        this.doSearchHandel()
      },
      /**
       * [doSearchHandel 查询列表接口]
       * @param  {[type]} _param [description]
       * @return {[type]}         [description]
       */
      doSearchHandel(_param) {
        if(!this.searchContent.defaultRange) {
          this.$message.warning("请选择下单时间范围")
          return
        }
        let beginTimeVal = this.searchContent.beginTime ? new Date(this.searchContent.beginTime.replace(/-/g, '/')).getTime() : 0
        let endTimeVal = this.searchContent.endTime ? new Date(this.searchContent.endTime.replace(/-/g, '/')).getTime() : 0
        if (this.searchContent.beginTime && this.searchContent.endTime && !isNaN(beginTimeVal) && !isNaN(endTimeVal)) {
          if ((endTimeVal - beginTimeVal) > 30 * 86400000 ) {
            this.$message.warning("下单开始时间至结束时间不能超过30天")
            return
          }
        }
        this.loading = true
        if(_param === true) {
          this.pageNum = 1
        }
        this.$http.post(requestUrl.getDeliveryOrderList, { ...this.searchContent, pageNum: this.pageNum, pageSize: this.pageSize }).then(res => {
          this.loading = false
          if(res.code === 0) {
            // 获取表格数据
            let tableDataArr = res.data && res.data.result && res.data.result.length ? res.data.result : []
            tableDataArr.forEach(item => {
              item.redirectUrl = `/order_mgt/detail/${item.orderId}`
            })
            this.tableData = tableDataArr
            this.total = res.data.total
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * @description: 批量发货
       * @param {type} 
       * @return: 
       */
      doBatchDelivery() {
        if(this.$refs.excelAdd) {
          let _val = {
            title: '批量发货',
            goodsIds: this.searchContent.goodsIds,
            tempConfig: this.tempConfig
          }
          this.$refs.excelAdd.setDefaultMsg(_val)
        }
      },
      /**
       * @description: 确认插入
       * @param {type} 
       * @return: 
       */
      doConfirmHandel(params) {
        let reqDataList = params.dataList.filter(item => {
          item.orderId = item.orderId.replace(' ', '')
          item.deliveryCode = item.deliveryCode.replace(' ', '')
          return item.orderId && item.companyCode && item.deliveryCode
        })
        if(reqDataList.length === 0) {
          this.$message.warning("请按照excel中要求填写模板中内容")
          return
        }
        this.$http.post(requestUrl.batchInsert, {list: reqDataList}).then(res => {
          if(res.code === 0) {
            this.$message.success("批量发货成功！")
            this.doSearchHandel()
            this.$refs.excelAdd.closeDisabled()
            this.$refs.excelAdd.closeDialog()
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.$refs.excelAdd.closeDisabled()
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })

      },
      businessTypeFormatter(row, column, cellValue) {
        let filterMsg = this.businessTypeOptions.filter(item => {
          return item.value === cellValue
        })[0]
        if(filterMsg) {
          return filterMsg.label
        }else {
          return '-'
        }
      },
      orderStatusFormatter(row, column, cellValue) {
        let filterMsg = this.orderStatusOptions.filter(item => {
          return item.value === cellValue
        })[0]
        if(filterMsg) {
          return filterMsg.label
        }else {
          return '-'
        }
      },
      showBatchInsert(row) {
        this.rowMsg = {...row}
        this.dialogVisible = true
        this.deliveryCode = ''
        this.companyCode = 1
      },
      orderDeliveryHandel() {
        if(!this.deliveryCode) {
          this.$message.warning("请输入物流单号")
          return
        }
        let reqDataList = []
        let params = {
          orderId: this.rowMsg.orderId,
          companyCode: this.companyCode,
          deliveryCode: this.deliveryCode
        }
        reqDataList.push(params)
        this.isDisabled = true
        this.$http.post(requestUrl.batchInsert, {list: reqDataList}).then(res => {
          this.dialogVisible = false
          setTimeout(() => {
            this.isDisabled = false
          }, 800);
          if(res.code === 0) {
            this.$message.success("发货成功！")
            this.doSearchHandel()
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.dialogVisible = false
          this.isDisabled = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      async exportData() {
        if(!this.searchContent.defaultRange) {
          this.$message.warning("请选择下单时间范围")
          return
        }
        let beginTimeVal = this.searchContent.beginTime ? new Date(this.searchContent.beginTime.replace(/-/g, '/')).getTime() : 0
        let endTimeVal = this.searchContent.endTime ? new Date(this.searchContent.endTime.replace(/-/g, '/')).getTime() : 0
        if (this.searchContent.beginTime && this.searchContent.endTime && !isNaN(beginTimeVal) && !isNaN(endTimeVal)) {
          if ((endTimeVal - beginTimeVal) > 30 * 86400000 ) {
            this.$message.warning("下单开始时间至结束时间不能超过30天")
            return
          }
        }
        let params = {
          ...this.searchContent,
          pageNum: 1,
          pageSize: 200
        }
        this.exportLoading = true
        let res = await Orders.searchHandel({...params})
        if(res.code === 0) {
          if(res.data && res.data.total) {
            let promiseAll = []
            let length = Math.ceil(res.data.total/200)
            for(let i = 0; i<length; i++) {
              params.pageNum = i + 1
              promiseAll.push(Orders.searchHandel({...params}))
            }
            Promise.all([...promiseAll]).then(result => {
              let allDataList = []
              result.forEach(item => {
                allDataList = [...allDataList, ...item.data.result]
              })
              allDataList.forEach(item => {
                item.orderStatusName = this.orderStatusOptions.filter(status => {
                  return status.value === item.orderStatus
                })[0].label
                item.businessTypeName = this.businessTypeOptions.filter(status => {
                  return status.value === item.businessType
                })[0].label
              })
              this.exportLoading = false
              if(allDataList && allDataList.length > 0) {
                const tHeader = utils.objectToArray(this.exportConfig, false)
                const filterVal = utils.objectToArray(this.exportConfig, true)
                exportSheet.ExportJsonToExcel({
                  header: tHeader,
                  filterVal,
                  data: allDataList,
                  filename: `${utils.timestampToTimeNoHMS(Date.parse(new Date()))}_批量发货导出订单`
                })
                this.$notify.success({
                  title: '温馨提示',
                  message: `${utils.timestampToTimeNoHMS(Date.parse(new Date()))}_批量发货导出订单成功`
                });
              }else {
                this.$notify.warning({
                  title: '温馨提示',
                  message: '没有查询到要导出的数据'
                });
              }
            }).catch(
              this.exportLoading = false
            )
          }else {
            this.exportLoading = false
            this.$notify.warning({
              title: '温馨提示',
              message: '没有查询到要导出的数据'
            });
          }
        }else {
          this.exportLoading = false
          this.$notify.error({
            title: '系统提示',
            message: res.msg || res.data.error || res.data.error_description
          });
        }
      },
      /**
       * @description: 下单时间范围发生变化
       * @param {type} 
       * @return: 
       */
      datePickerRangeChange(result) {
        this.searchContent.defaultRange = result ? [...result] : result
        this.searchContent.beginTime = result ? result[0] : ''
        this.searchContent.endTime = result ? result[1] : ''
        this.doSearchHandel(true)
      }
      
    }
  }
</script>


