import ajax from '../utils/request'
const requestUrl = {
  // 获取列表
  getDeliveryOrderList: '/plateform/a/dc/order/list'
}
const searchHandel = (params) => {
  return new Promise((resolve, reject) => {
    ajax.post(requestUrl.getDeliveryOrderList, {...params}).then(res => {
      return resolve(res)
    }).catch(e => {
      return reject(e.message)
    })
  })
}

export default {
  searchHandel
}



